.form-element {
  position: relative;
  font-size: 18px;
  line-height: 24px;
  &__label {
    position: absolute;
    line-height: inherit;
    top: 20px;
    left: 20px;
    pointer-events: none;
    transition: transform $trs1, color $trs1, font-size $trs1;
    color: #4A4A4A;
    z-index: 2;
  }
  &__input {
    position: relative;
    display: block;
    line-height: inherit;
    appearance: none;
    outline: none;
    box-shadow: none;
    background-color: #fff;
    border: 2px solid #D5D5D5;
    color: #000;
    transition: border-color $trs1;
    border-radius: 0;
    cursor: pointer;
    width: 100%;
    padding: 27px 18px 9px 18px;
    &:-webkit-autofill,
    &:-webkit-autofill:hover, 
    &:-webkit-autofill:focus {
      -webkit-text-fill-color: #000;
      box-shadow: 0 0 0px 1000px transparent inset;
      transition: background-color 5000s ease-in-out 0s;
    }
    &:focus {
      cursor: text;
    }
  }
  textarea {
    min-height: 136px;
  }
  .errorlist {
    font-size: 14px;
  }

  &.focus, &.filled, &.active {
    .form-element__label {
      transform: translateY(-14px);
      font-size: 12px;
      color: $color-accent;
    }
    .form-element__input {
      border-color: $color-accent;
    }
  }

  &.error {
    .form-element__label {
      color: $color-error;
    }
    .form-element__input {
      border-color: $color-error;
    }
  }

  &_small {
    font-size: 16px;
    line-height: 22px;
    .errorlist {
      font-size: 12px;
    }
    .form-element__label {
      top: 10px;
      left: 12px;
      background-color: #fff;
      padding: 0 4px;
    }
    .form-element__input {
      padding: 9px 14px 7px 14px;
    }
    &.focus, &.filled, &.active {
      .form-element__label {
        transform: translateY(-20px);
      }
    }
  }
}

//selects
.select2-container--new-select {
  * {
    outline: none !important;
  }
}
.select2.select2-container--new-select {
  width: 100% !important;
}

.select2-container--new-select {
  .select2-selection--single {
    height: 64px;
    display: block;
    border: 2px solid #D5D5D5;
    color: #000;
    transition: border-color $trs1;
    border-radius: 0;
    cursor: pointer;
    padding: 27px 18px 9px 18px;
  }
  .select2-selection__clear {
    display: none;
  }
  .select2-selection__arrow {
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10px 8px 0;
    border-color: #4a4a4a transparent transparent;
    position: absolute;
    right: 16px;
    top: 28px;
    transition: transform $trs1;
  }
  //dropdown
  .select2-dropdown {
    border-style: solid;
    border-color: #D5D5D5;
    margin: 0;
    &--above {
      border-width: 2px 2px 0 2px;
    }
    &--below {
      border-width: 0 2px 2px 2px;
    }
  }
  .select2-results {
    cursor: default;
  }
  .select2-results__options {
    overflow: auto;
    max-height: 200px;
  }
  .select2-results__option {
    line-height: 20px;
    padding: 15px 18px;
    cursor: pointer;
    transition: background-color $trs1, color $trs1;
    &:hover, &[aria-selected="true"] {
      background-color: #fff;
      color: #000 !important;
    }
    &[aria-disabled="true"] {
      pointer-events: none;
      opacity: 0.5;
    }
  }
}

.form-element {
  &.filled, &.focus {
    .select2-container--new-select .select2-selection--single {
      border-color: $color-accent;
    }
  }
  &.focus {
    .select2-container--new-select .select2-selection__arrow {
      transform: rotate(180deg);
    }
  }
  &.error {
    .select2-container--new-select .select2-selection--single {
      border-color: $color-error;
    }
  }
  &_small {
    .select2-container--new-select {
      .select2-selection--single {
        padding: 9px 14px 7px 14px;
        height: 42px;
      }
      .select2-results__option {
        padding: 10px 14px;
        font-size: 14px;
      }
      .select2-selection__arrow {
        top: 19px;
        border-width: 8px 7px 0;
      }
    }
  }
}

.form-checkbox {
  position: relative;
  &__input {
    display: none;
    &:checked ~ .form-checkbox__label {
      &:before {
        background-color: $color-accent;
      }
      &:after {
        opacity: 1;
      }
    }
  }
  &__label {
    margin: 0 !important;
    display: block !important;
    padding: 0 24px 0 30px;
    line-height: 20px;
    position: relative;
    cursor: pointer;
    &:before {
      content: '';
      display: block;
      width: 20px;
      height: 20px;
      border: 2px solid $color-accent;
      background-color: #fff;
      position: absolute;
      left: 0;
      top: 0;
      transition: background-color $trs1;
    }
    &:after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 5px;
      left: 4px;
      width: 12px;
      height: 7px;
      border-width: 0 0 2px 2px;
      transform: rotate(-45deg);
      border-style: solid;
      border-color: #fff;
      opacity: 0;
      transition: opacity $trs1;
    }
  }
}

.form-radio {
  position: relative;
  &__input {
    display: none;
    &:checked ~ .form-radio__label {
      &:before {
        background-color: $color-accent;
      }
      &:after {
        opacity: 1;
      }
    }
  }
  &__label {
    margin: 0 !important;
    display: block !important;
    padding: 0 24px 0 30px;
    line-height: 20px;
    position: relative;
    cursor: pointer;
    &:before {
      content: '';
      display: block;
      width: 20px;
      height: 20px;
      border: 2px solid $color-accent;
      border-radius: 20px;
      background-color: #fff;
      position: absolute;
      left: 0;
      top: 0;
      transition: background-color $trs1;
    }
    &:after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 6px;
      left: 6px;
      width: 8px;
      height: 8px;
      border-radius: 10px;
      background-color: #fff;
      opacity: 0;
      transition: opacity $trs1;
    }
  }
}



.errorlist {
  margin: 4px 0 0 0;
  padding: 0;
  list-style: none;
  color: $color-error;
  font-size: 14px;
  line-height: 18px;
  li {
    &:not(:last-child) {
      margin-bottom: 2px;
    }
  }
}


.n-form {
  &__container {
    margin-bottom: -20px;
  }
  &__item {
    margin-bottom: 20px;
  }
  &__button {
    width: 100%;
  }
  & > p {
    &:first-child {
      margin-bottom: 25px;
    }
    &:last-child {
      margin-top: 25px;
    }
  }
}

.js-calendar {
  position: relative;
  &.active {
    &:after {
      transform: rotate(180deg);
    }
  }
  &::after {
    content: '';
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10px 8px 0;
    border-color: #4a4a4a transparent transparent;
    position: absolute;
    right: 16px;
    top: 28px;
    transition: transform $trs1;
  }
  &.form-element_small {
    &:after {
      top: 19px;
      border-width: 8px 7px 0;
    }
  }
}